	module.exports = {"data":{"markdownRemark":{"html":"<p>This site conducts display test in the following Web browser.</p>\n<h2>◆ Completely compatible Web browser list</h2>\n<ul>\n<li>\n<p><a href=\"http://windows.microsoft.com/ja-JP/internet-explorer/download-ie\">Microsoft Internet Explorer</a> 10, 11 [including Modern UI version since 10]</p>\n</li>\n<li>\n<p><a href=\"http://jp.opera.com/\">Opera</a> 28</p>\n</li>\n<li>\n<p><a href=\"http://support.apple.com/kb/DL1531?viewlocale=ja_JP\">Apple Safari</a> 8</p>\n</li>\n<li>\n<p><a href=\"http://www.google.co.jp/intl/ja/chrome/browser/\">Google Chrome</a> 41.0 [Windows version · Linux version]</p>\n</li>\n<li>\n<p><a href=\"http://www.mozilla.jp/firefox/\">Mozilla Firefox</a> 37 [Windows version]</p>\n<h2>◆ List of Web browsers compatible with nearly no problem level</h2>\n</li>\n<li>\n<p>PSVITA Browser (There is a noncompliant content in a small part)</p>\n</li>\n<li>\n<p><a href=\"http://windows.microsoft.com/ja-JP/internet-explorer/download-ie\">Microsoft Internet Explorer</a> 9 [Windows Phone 7.5 - 7.8, including XBOX 360] (Some display is wrong)</p>\n<h2>◆ Web browsers that correspond to the minimum degree of readability</h2>\n</li>\n<li>\n<p><a href=\"http://www.microsoft.com/ja-jp/windows/ie/downloads/ie6sp1/default.aspx\">Microsoft Internet Explorer</a> ~ 8 (There are large layout collapses in some places.I will make it out of the basic support subject, but I am planning to correspond to the extent that I can read it for the time being)</p>\n</li>\n<li>\n<p><a href=\"http://w3m.sourceforge.net/\">w3m</a> 8.5.2 [Linux version] (It is a text browser, but you can read articles properly)</p>\n</li>\n<li>\n<p><a href=\"http://lynx.browser.org/\">lynx</a> 2.8 [Linux version] (the same sentence below)</p>\n</li>\n<li>\n<p><a href=\"http://elinks.or.cz/\">ELinks</a> 0.12 pre5 [Linux version] (the same sentence below)</p>\n</li>\n<li>\n<p><a href=\"http://www.android.com/\">Android</a> browser [Chrome OK]</p>\n<h2>◆ List of extra Web browsers</h2>\n</li>\n<li>\n<p><a href=\"http://www.w3.org/Amaya/\">amaya</a> 11.4.4 (Javascript can be <a href=\"http://www.w3.org/Amaya/\">overwhelmed,</a> it will not be the same as it is at present ...... Hi this browser, I was still active)</p>\n</li>\n<li>\n<p><a href=\"http://www.jikos.cz/~mikulas/links/\">Links</a> 2.2 (violently garbled)</p>\n</li>\n<li>\n<p><a href=\"http://elinks.or.cz/\">ELinks</a> 0.11.6, 0.12 pre5 [Build for Windows] (Violently garbled) If this browser is displayed properly, or some of the display is disordered, but I could read it once, or if it is not this , It is very much appreciated if you leave a comment on the corresponding browser name and version information. It is the place I want to test whether it is displayed properly on <a href=\"http://k-tai.hitachi.jp/pc/lineup.html\">au WIN W 43 H</a> , but I have not tested it yet because I lost the terminal somewhere. (Although there is no doubt that it is somewhere in the room ......) By the way, this web page depends on the page, but I got 85 ~ 91 points by <a href=\"http://www.htmllint.net/\">Another HTML-lint 5</a> . (Full marks or equivalent Miuri Po)</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-11-27T23:20:39+09:00","strDate":"2012/11/27","title":"Web browser compatibility"}}},"pathContext":{"path":"/en/blog/2012/11/27-browser/","langKey":"en"}}

